<template>
	<div id="main">
	  <div id="content" v-html="content"></div>
	</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Main',
  data () {
    return {
      content: '<h2>Hello! Welcome come BuErGuo</h2>'
    }
  },
  watch: {
    '$route' (to, from) {
      console.log(to)
      let that = this
      let url = '/static/' + to.params.folder + '/' + to.params.filename
      // 请求 html 文件
	  axios.get(url).then(function (res) {
        console.log(res)
        that.content = res.data
      })
    }
  }
}
</script>

<style>
#main {
  padding-right: 160px;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
}
#content {
  padding: .8rem 1.3rem;
}
#table-of-contents {
  width: 160px;
  position: fixed;
  top: 60px;
  right: 0;
  height: 100%;
  font-size: .6rem;
  overflow-y: auto;
}
</style>



