<template>
  <nav id="nav">
	<ul>
		<li>
			<router-link to="/folder/index.html">Website</router-link>
		</li>
		<li>
			<router-link to="/folder/emacs-keyboard-shortcuts.html">Emacs Keyboard</router-link>
		</li>
	</ul>
  </nav>
</template>

<script>
	export default {
		name: 'SideMenu',
		data () {
			return {
				menu: {
					folder: [
						'index.html'
					]
				}
			}
		}
	}
</script>
<style>
  #nav {
    width: 300px;
	height: 100%;
    position: fixed;
	top: 60px;
	left: 0;
	z-index: 99;
	background-color: #66cc66;
  }
</style>



